* {
  font-family: "Roboto";
}

html, #root, body {
  min-width: 1450px;
  width: 100%;
  height: 100%;
  margin: 0px;
}

 {
  /* display: flex;
  flex-direction: row; */
}

html {
  background-color: rgb(0, 0, 0);
}

body {
  /* position: fixed; */
  overflow: hidden;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

textarea {
  resize: none !important;
}

.sidebar {
  z-index: 10;
  position: absolute;
  left: 0px;
  width: 420px;
  height: 100%;
  border-right: solid 1pt;
  background-color: rgb(222, 225, 220);
  box-sizing: border-box;
  padding: 16px 16px;
  overflow-y: scroll;
}

p {
  font-size: 18px;
  line-height: 1.8em;
}

td {
  text-align: center;
}

#scroller {
  height: 100%;
  width: 100%;
  overflow-y: scroll;
}

.PageContents {
  width: 100%;
  height: 100%;
}

.right {
  width: calc(100% - 420px);
  height: 100%;
  float: right;
  overflow-y: scroll;
}

.gif-Grid {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  /* flex-wrap: wrap; */
}

.sourcesTable {
  display: flex;
  width: 100%;
  height: 100%;
  justify-content: center;
  /* align-items: center; */
}
